.footer-one-line {
  border-top: 1px solid $grey-border;
  box-shadow: 0 -55px 0 0 $grey-background;
  background-color: $grey-background;

  a:hover {
    color: $light-blue;
  }
}

.footer-one-line__wrap {
  display: flex;
  justify-content: space-between;
  padding-left: 25px;
  padding-right: 29px;
  max-width: 1467px;

  @media (max-width: $mobile-width-only) {
    margin-top: 6px;
    padding-bottom: 26px;
    padding-right: 0;
  }

  p {
    margin-top: 14px;
    margin-bottom: 14px;
    font-family: "PT Mono", "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    line-height: 24px;
    letter-spacing: 0.04em;
    color: $grey-letter;
    text-transform: uppercase;


    @media (max-width: $tablet-width-only) {
      margin-top: 21px;
      margin-bottom: 24px;
      text-align: center;
    }

    @media (max-width: $mobile-width-only) {
      margin-bottom: 0;
      text-align: center;
    }
  }

  @media (max-width: $mobile-width-only) {
    display: block;
    margin-left: -16px;
  }
}

.footer-one-line__wrap-desktop {
  display: flex;
  justify-content: space-between;
  width: 457px;

  @media (max-width: $tablet-width-only) {
    display: none;
  }
}

.footer-one-line__wrap-middle {
  display: flex;
  justify-content: space-between;
  max-width: 816px;
  width: 70%;

  @media (max-width: $tablet-width-only) {
    width: 217px;
  }

  @media (max-width: $mobile-width-only) {
    margin-left: auto;
    margin-right: auto;
  }
}
